import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';
import { createStore } from 'redux';
import {Provider} from 'react-redux'

//REDUX = Actions,Reducer,Store,Dispatch

//Selector 
const ROYALENFIELD="ROYALENFIELD";
const NS200="NS200";
const SUBSCRIBE="SUBSCRIBE";

//Actions
export const reAction=()=>{return{ type : ROYALENFIELD }}
export const nsAction=()=>({type:NS200})
export const subscribeAction=()=>({type:SUBSCRIBE})

const initialValue={
        img:"data:image/png;base64,"
        ,isSubscribed:false,
}

//Reducer
const bikeReducer=(state=initialValue,actions)=>
{
  switch (actions.type) 
  {
    case ROYALENFIELD:
      return(
      {
        ...state,
        img:"https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSUstKsQWcn-iWUlj46JyK9FTG-ftiVk7eBiA&usqp=CAU"
      })
    case NS200:
      return({
        ...state,
        img: "data:image/jpeg;base64,"
      }) 
     case SUBSCRIBE:
        return (
          {
          ...state,
          isSubscribed:!state.isSubscribed
        }

        ) 
        
    default: 
       return{
         ...state 
        }
  }
}

//Store
const store=createStore(bikeReducer)
//store.subscribe(()=>{console.log(store.getState())});//just to view

//Dispatch

//store.dispatch(reAction())
//store.dispatch(nsAction())


ReactDOM.render(

  <Provider store={store}>
   <App />
  </Provider>
 , document.getElementById('root')
);
reportWebVitals();


