import React from 'react';
import './App.css';
import { useDispatch, useSelector} from 'react-redux';
import { reAction ,nsAction ,subscribeAction } from './index';


function App()
{

  const bikes = useSelector(state => state);
  const dispatch = useDispatch();
  return (
    <div className="con">
  
      <p>{bikes.isSubscribed?"Welcome... Thankyou for accessing":"Click on subscribe to enter...!!!"}</p>
    <img src={bikes.img} alt="" />
     <br/>
       <button className="buttons" disabled={!bikes.isSubscribed} onClick={() => dispatch(reAction())} >show RoyalEnfield</button>
        <button className="buttons" disabled={!bikes.isSubscribed} onClick={() => dispatch(nsAction())} >show NS200</button>
       <button className="buttons" onClick={() => dispatch(subscribeAction())}>{!bikes.isSubscribed ?"subscribe":"unsubscribe"} </button>
    
     
     
     </div>
  );
}
export default App;
